import http from '@/components/utils/http.js';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			server:this.apiServer,
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			goodsList: [],
			salegrade:1,
			title:"直升产品列表"
		};
	},
	
	onLoad(options){
		
		 



		if(options.salegrade!=undefined){
			this.salegrade=options.salegrade;
		}
		
		this.loadData();
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.loadData();
	},
	methods: {
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add', loading) {
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			
			//let goodsList = await this.$api.json('goodsList');
			let opts = {
				url: '/productApi/upgradeproductlist/',
				method: 'post'
			};
			
			let param = {salegrade:this.salegrade};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					this.title=res.data["g"][Number.parseInt(this.salegrade)+1]+this.title;
					if (res.data['code'] == 0) {
						let goodsList = res.data["plist"];
						if(type === 'refresh'){
							this.goodsList = [];
						}
						//筛选，测试数据直接前端筛选了
						if(this.filterIndex === 1){
							goodsList.sort((a,b)=>b.sales - a.sales)
						}
						if(this.filterIndex === 2){
							goodsList.sort((a,b)=>{
								if(this.priceOrder == 1){
									return a.price - b.price;
								}
								return b.price - a.price;
							})
						}
						
						this.goodsList = this.goodsList.concat(goodsList);
						
						//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
						this.loadingType  = this.goodsList.length== 0 ? 'nomore' : 'more';
						if(type === 'refresh'){
							if(loading == 1){
								uni.hideLoading()
							}else{
								uni.stopPullDownRefresh();
							}
						}
						
					} else {
						uni.showToast({title: res.data.msg,icon: 'none'});
					}
				},
				error => {
					console.log(error);
				}
			);
			
			
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			this.cateId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			//测试数据没有写id，用title代替
			let psid = item.psid;
			uni.navigateTo({
				url: `/pages/product/product?psid=${psid}`
			})
		},
		stopPrevent(){}
	},
}
